@import url("https://fonts.googleapis.com/css?family=Lato:400,700&display=swap");
@import url("https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap");
@import url("https://fonts.googleapis.com/css?family=apercu:500,700&display=swap");

.header {
  background-color: #f5f5dc;
  color: #282c34;
  font-family: "apercu", sans-serif;
}

.block {
  display: flex;

  flex-direction: row;
  flex-wrap: nowrap;
  align-items: baseline;
}

.tagline {
  height: 70vh;
  text-align: center;
}
.maimPage {
  height: 100vh;
}
.tagline div {
  margin: 0px;
  padding: 0px;
}
.tagline .slogan {
  font-family: "apercu", sans-serif;
  font-weight: 500;
  font-size: 50px;
  text-transform: uppercase;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  padding-top: 5%;
}

.slogan p {
  margin: 0px;
  padding: 0px;
}
.slogan .sl1 {
  width: 50%;
  text-align: end;
}
.slogan .sl2 {
  width: 50%;
  padding-left: 50%;
  text-align: start;
}
.tagline .subSlogan {
  color: #c0c0c0;
  text-align: end;
  padding-right: 5%;
}
