.text {
  width: 50vw;
}
.image {
  width: 50vw;
}
.image img {
  width: 290px;
  height: auto;
  padding: 10px;
}

.imageGym img,
.imageOffice img {
  width: 310px;
  height: auto;
}
.office {
  height: 100vh;
  position: relative;
}

.imageOffice .img3_1 {
  position: absolute;
  top: 10%;
  right: 2%;
}
.imageOffice .img3_2 {
  position: absolute;
  left: 30%;
  bottom: 0;
}
.imageOffice .img3_3 {
  position: absolute;
  left: 40%;
}
.imageOffice .img3_4 {
  position: absolute;
  left: 2%;
  top: 25%;
}

.text .officeText {
  position: absolute;
  right: 5%;
  bottom: 5%;
  width: 500px;
  text-align: justify;
}

.title {
  font-family: "apercu", sans-serif;
  font-weight: bold;
  font-size: 50px;
  text-transform: uppercase;
  padding-left: 4%;
}

.subtitle {
  color: #c0c0c0;
  padding-left: 2%;
  text-transform: uppercase;
  font-size: 10px;
}

.text .printText {
  width: 50%;
  padding-left: 50%;
  padding-top: 30px;
}

.imageArea .img2_1 {
  height: auto;
  width: 30%;
}

.imageArea .img2_4 {
  height: auto;
  width: 30%;
}

.imageArea {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}
.img2 .img2_2,
.img2 .img2_3 {
  height: auto;
  width: 250px;
}
.text {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.img2 {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-evenly;
}

.areaText {
  padding-left: 15px;
  padding-right: 15px;

  text-align: justify;
}

.gym {
  height: 100vh;
  position: relative;
}

.imageGym .img4_1 {
  position: absolute;
  top: 50%;
  right: 10%;
}
.imageGym .img4_2 {
  position: absolute;
  left: 5%;
  bottom: 10%;
}
.imageGym .img4_3 {
  position: absolute;
  left: 35%;
  top: 20%;
  width: 400px;
  height: auto;
}
.imageGym .img4_4 {
  position: absolute;
  right: 2%;
  top: 0;
}

.text .gymText {
  position: absolute;
  left: 5%;
  top: 5%;
  width: 350px;
  text-align: justify;
}

.black {
  background-color: #282c34;
  color: #f5f5dc;
  font-family: "apercu", sans-serif;
  font-weight: bold;
  font-size: 36px;
  text-transform: uppercase;
  padding-top: 20px;
  padding-bottom: 20px;
  text-align: center;
}

@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

